.info {
  margin-bottom: 150px;
  background-image: url("../img/info-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include desktop {
    margin-bottom: 100px;
  }

  @include tablet {
    margin-right: -15px;
    margin-left: -15px;
  }

  &__col {
    padding: 70px;
    background-color: var(--accent-2);

    @include big-desktop {
      padding: 50px 15px 20px 0;
    }

    @include tablet {
      padding: 50px 15px;
      max-width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include tablet {
      font-size: 24px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--text-bg);

    @include tablet {
      font-size: 14px;
    }
  }

  &__link {
    display: inline-flex;
    align-items: center;
    padding: 30px 35px;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--belyy);
    background: var(--siniy);
    transition: opacity .3s ease-in-out;
    gap: 15px;

    @include hover {
      opacity: .8;
    }

    @include tablet {
      font-size: 13px;
      text-align: center;
    }

    &::after {
      content: "";
      display: block;
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 61px;
      width: 25px;
      height: 25px;
      background-image: url("../img/arr-btn.svg");
      background-position: center;
      background-size: 7px 11px;
      background-repeat: no-repeat;
    }
  }

  &__img {
    display: flex;
    justify-content: center;
    padding: 10px 0;
    width: 100%;

    @include tablet {
      display: none;
    }
  }
}
